<template>
    <div class="contact-form router-content">
        <h2>Contact/Commision Enquiry</h2>
        <p>
            As a UK based printer enthusiast, you can use this form to contact me with your 3D printing questions or commisions.
        </p>
        <p>
            If you add in as much detail as you can and I'll get back to you as soon as I can.
        </p>
        <MaterialInput inputName="email" label="E-mail"></MaterialInput>
        <MaterialInput inputName="details" label="Details" :multiline="true" :lines="15"></MaterialInput>
        <div class="button-holder">
            <a class="button">Submit</a>
        </div>
    </div>
</template>

<script>
export default {
  name: 'contact'
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .contact-form {
        display: flex;
        flex-direction: column;

        .button-holder {
            margin-top: 25px;
            margin-left: auto;
            margin-right: auto;
            width: 75%;
            text-align: right;

            a {
                &.button {
                    margin-left: auto;
                    text-decoration: none;
                    padding: 5px 15px;
                    border: 1px solid $primaryColour;
                    background-color: $primaryColour;
                    width: 150px;
                    cursor: pointer;
                    border-radius: 3px;
                }

                &:hover {
                    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                }
            }
        }
    }
</style>



